.line-break {
    background: #ddd;
    border: 1px solid #ddd;
}

#main-body {
    /* padding-top: 100px; */

    /* height: 85vh; */
    display: flex;
    flex-direction: row;
    justify-content: center;
    /* overflow: auto; */
}

#start-float-header {
    position: -webkit-sticky; /* Safari & IE */
    position: sticky;
    top: 0;
    width: 100%;
    z-index: 1;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.12);
}

#main-body-inner {
    margin: 1em 0;
    width: 70%;
    padding: 1em 0em;
    padding-bottom: 5em;
    display: flex;
    flex-direction: column;
    gap: 6em;
    height: 100%;
}

#main-body-page {
    display: flex;
    flex-direction: column;
    gap: 4em;
}

#chat-box-button {
    position: fixed;
    right: 30px;
    bottom: 30px;
    height: 50px;
}

#chat-box-button:hover {
    box-shadow: 0px 0px 10px #eee;
    cursor: pointer;
    padding: 10px;
}

.inner_button {
    padding: 0.5em;
    width: fit-content;
    border: none;
    background: #fff;
    outline: none;
    color: rgba(0, 0, 0, 0.7);
    border: 1px solid #ddd;
    cursor: pointer;
}

.inner_button:hover {
    background: #495DFF;
    color:rgba(255, 255, 255, 0.7);
    font-weight: bold;
    border: 1px solid #fff;
}

#page-index-container {
    position: fixed;
    left: 1vw;
    top: 40vh;
    display: flex;
    flex-direction: column;
    gap:1em;
}

#page-index-container button {
    background:none;
    border: none;
    opacity: 0.7;
    font-size: 15px;
    text-align: left;
    outline: none;
}

#page-index-container button:hover {
    opacity: 1;
    cursor: pointer;
}