#grey-header button {
    border: none;
    outline: none;
}

#grey-header {
    background-color: #fff;
    width: calc(100%-1em);
    padding: 0.8em 1em;
    height: fit-content;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0;
    /* align-items: center; */
}

#grey-header #social-icons-container, #pages-container {
    display: flex;
    gap: 0.8em;
    align-items: center;
}


#grey-header #social-icons-container img {
    width: 18px;
    height: 18px;
    opacity: 0.7;
    cursor: pointer;
}

#grey-header #social-icons-container img:hover {
    opacity: 1;
}

.nav-bar-button  {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    padding: 0;
    height: fit-content;
    background: none;
    border: none;
    outline: none;
    color: rgba(0, 100, 200, 0.7);
    cursor: pointer;
    padding: 1em;
}

.nav-bar-button:hover {
    color: rgba(255, 255, 255, 0.7);
    background-color: rgba(0, 100, 200, 1);
}

#main-header {
    margin: 0;
    display: grid;
    grid-template-columns: auto 10fr auto;
    gap: 1em;
    padding: 1em;
    align-items: center;
    height: fit-content;
    background: #FFFFFF;
    border-bottom: 3px solid #EEEEEE;
    box-sizing: border-box;
}

#grey-header #logo {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    /* line-height: 105px; */
    letter-spacing: 0.015em;
    color: rgba(0, 100, 200, 0.7);
}

#start-nav-bar {
    display: flex;
    flex-direction: row;
    gap: 1em;
    position: relative;
    align-items: center;
}



#start-nav-bar button:hover {
    opacity: 1;
}

#start-nav-bar #big-purple {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    padding: 10px;
    background: #495DFF;
    color: #fff;
    opacity: 1;
}





#drop-down-container {
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    position: absolute;
    top: 55px;
    background-color: rgba(0, 100, 200, 1);
}

#drop-down-container button {

    padding: 1em;
    font-weight: 700;
    background-color: rgba(0, 0, 0, 0);
    color: #fff;

}

#drop-down-container button:hover {
    /* background-color:  #eee     */
}

